import React from 'react';
import './intromobile.css'

function IntroMobile() {
  return (
    <div className="intro_mobile_body">
      <img position="absolute" z-index="0" src="cal_poly_background.jpg" alt="Cal Poly Campus" width="100%" margin="0"/>
      <div className="name_mobile_div">
        <p className="name_mobile"> Connor Owens</p>
      </div>

        <img className="profile_pic_mobile" src="profile_pic.JPG" alt="Connor Owens"/>
      
      <div className="connor_info_mobile">
        <p className="heading_2_mobile">Computer Engineering Student</p>
        <img className="cal_poly_logo_mobile" src="cal_poly_logo.png" alt="Cal Poly"/>
      </div>
    </div>
  )
}

export default IntroMobile;
