import React from 'react';
import Intro from './Intro.js';
import IntroMobile from './intromobile.js';
import AboutMe from './aboutme.js';
import Links from './links.js';
import Skills from './skills.js';
import Projects from './projects.js';
import ProjectsMobile from './projectsmobile.js';
import Coursework from './coursework.js';

class WebsiteContents extends React.Component {

  constructor(props) {
    super(props)

    this.state = {
      width: 0,
      height: 0
    }
    this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
  }

  componentDidMount() {
    this.updateWindowDimensions();
    window.addEventListener('resize', this.updateWindowDimensions);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.updateWindowDimensions);
  }

  updateWindowDimensions() {
    this.setState({ width: window.innerWidth, height: window.innerHeight });
  }

  render(){

    if (this.state.height >= 1.14*this.state.width) {
      return (
        <div>
          <IntroMobile />
          <AboutMe />
          <Links />
          <Skills />
          <ProjectsMobile />
          <Coursework />
        </div>
        )
    }else{
      return (
        <div>
          <Intro />
          <AboutMe />
          <Links />
          <Skills />
          <Projects />
          <Coursework />
        </div>
        )
    }
  }
}

export default WebsiteContents;
