import React from 'react';
import './links.css';
import './font.css';
import Button from 'react-bootstrap/Button';

function Links() {
  return(
    <div className="links_body">
      <p className="heading_text">
        Links
      </p>
      <Button className="link_button" href="https://github.com/ConnorOwens" variant="outline-success"><p width="100%">GitHub</p> <div width="100%"><img className="link_picture" alt="Github Logo" src="Github.png"/></div></Button>
      <Button className="link_button" href="https://www.linkedin.com/in/ConnorGOwens/" variant="outline-success"><p width="100%">LinkedIn</p> <div width="100%"><img className="link_picture" alt="LinkedIn Logo" src="linkedin.png"/></div></Button>
      <Button className="link_button" href="https://connorowens.github.io/resume.pdf" variant="outline-success"><p width="100%">Resume</p> <div width="100%"><img className="link_picture" alt="Resume Logo" src="resume.png"/></div></Button>
      <Button className="link_button" href="mailto:owensconnor1@gmail.com" variant="outline-success"><p width="100%">Email</p> <div width="100%"><img className="link_picture" alt="Email Logo" src="email.png"/></div></Button>
    </div>
  )
}

export default Links;
